.img-component-container {
    cursor: pointer;
    height: 200px;
    overflow:auto;
} 

.images-list { 
    display: flex;
    flex-direction: row;
    list-style-type: none;
    padding-left: 0px;
    flex-wrap:wrap;
    justify-content:center;
}

.image-container {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.hero-img {
    margin-left: -26px;
    border-radius: 50%;
    border: solid 3px white;
}

#img-name-tooltip {
    font-size: 12px;
}

/* width */
::-webkit-scrollbar {
    width: 6px;
}

/* Track */
::-webkit-scrollbar-track {
    background: #f1f1f1;
}

/* Handle */
::-webkit-scrollbar-thumb {
    background: #888;
    border-radius: 999px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background: #555;
}